<template>
	<div class="page" v-if="totalPageNum>0">
        <span class="prev" v-on:click="currPage>1 ? changePage(currPage-1) : null" v-bind:class="currPage > 1 ? 'curr' : 'disable'">Prev</span>
        <span v-on:click="changePage(1)" v-bind:class="{'curr':currPage == 1}">1</span>
        <span v-if="currPage-showPageHalfNum>=3" class="dots">...</span>
        <span v-for="item in showPage" v-if="item>=2&&item<=totalPageNum-1" v-on:click="changePage(item)" v-bind:class="{'curr':item == currPage}">{{item}}</span>
        <span v-if="currPage+showPageHalfNum<=totalPageNum-2" class="dots">...</span>
        <span v-on:click="changePage(totalPageNum)" v-bind:class="{'curr':currPage == totalPageNum}" v-if="totalPageNum>=2">{{totalPageNum}}</span>
        <span class="next" v-on:click="currPage<totalPageNum ? changePage(currPage+1) : null" v-bind:class="currPage<totalPageNum ? 'curr' : 'disable'">Next</span>
    </div>
</template>

<script>
	export default {
		props:['totalPageNum','currPage'],
		data:function(){
			return {
				// totalPageNum:10,
				// currPage:1,
				showPage:[],
				showPageHalfNum:2
			}
		},
		created:function(){
			this.initPage()
		},
		watch:{
			totalPageNum:function(){
				this.initPage()
			},
			currPage:function(){
				this.initPage()
			}
		},
		methods:{
			initPage:function(){
				this.showPage = []
		        for(var i = -this.showPageHalfNum;i<=this.showPageHalfNum;i++){
		          this.showPage.push(this.currPage+i)
		        }
			},
			changePage:function(val){
				// this.currPage = val
				this.$emit('changePageChild',val)
			}
		}
	}
</script>

<style scoped>
	.page{text-align: center;width: 100%;-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	-o-user-select: none;
	user-select: none;}
	.page span{font-size: 14px;color: #c0c0c0;display: inline-block;margin: 0 5px;cursor: pointer;}
	.page span.dots{font-size: 20px;}
	.page span.prev,.page span.next{background-color: #d1d1d1;padding: 2px 8px;border-radius: 2px;color: #fff;}
	.page span.curr{color: #413e3f;}
	.page span.prev.curr,.page span.next.curr{background-color: #0096e7;color: #fff;}
	.page span.prev.disable,.page span.next.disable{cursor: not-allowed;}
</style>


