<template>
	<div>
		<canvas ref="myCanvas"></canvas>
		<div><br></div>
		<label>start: <input type="text" v-model="start"></label>
		<label>end: <input type="text" v-model="end"></label>
    <label><input type="button" value="OK" v-on:click="drawCircleAnimate([parseInt(start),parseInt(end)])"></label>
	</div>
</template>

<script>

export default {
  name: 'progress',
  data:function(){
  	return {
  		canvas:null,
  		ctx:null,
  		width:80,
  		height:110,
  		lineWidth:6,
  		strokeStyle1:'#a3c8db66',
  		strokeStyle2:'#00aee6',
  		font:"300 24px 'Muli',sans-serif,Arial,Helvetica",
  		fillStyle1:'#26324b',
  		fontPosition:100,
  		start:0,
  		end:50
  	}
  },
  created:function(){
  	this.$nextTick(function(){
  		this.canvas = this.$refs.myCanvas
  		this.canvas.width = this.width
      this.canvas.height = this.height
      this.ctx = this.canvas.getContext("2d")
  		this.drawCircleAnimate([this.start,this.end])
  	})
  },
  methods:{
  	drawCircle:function(obj){
  		// 清除
  		this.ctx.clearRect(0,0,this.width,this.height)
  		// 底色圆环
        this.ctx.beginPath()
        this.ctx.lineWidth = this.lineWidth
        this.ctx.strokeStyle = this.strokeStyle1
        this.ctx.arc(this.width/2,this.width/2,this.width/2-this.lineWidth,-Math.PI/2,Math.PI*3/2)
        this.ctx.stroke()
        // 进度环
        this.ctx.beginPath()
        this.ctx.lineWidth = this.lineWidth
        this.ctx.strokeStyle = this.strokeStyle2
        this.ctx.arc(this.width/2,this.width/2,this.width/2-this.lineWidth,Math.PI*2*obj[0]/100-Math.PI/2,Math.PI*2*obj[1]/100-Math.PI/2)
        this.ctx.stroke()
        // 进度数
        this.ctx.beginPath()
        this.ctx.font = this.font
        this.ctx.textBaseline = 'middle'
        this.ctx.textAlign = 'center'
        this.ctx.fillStyle = this.fillStyle1
        this.ctx.fillText((obj[1]-obj[0])+'%', this.width/2, this.fontPosition)
    },
    drawCircleAnimate:function(obj){
    	if(obj.length<3){
    		var obj = [obj[0],obj[0],obj[1]]
    	}else{
    		var obj = obj
    	}
    	requestAnimationFrame(function(){
    		obj[1]+=1
    		this.drawCircle([obj[0],obj[1]])
    		if(obj[1]<obj[2]){
    			this.drawCircleAnimate(obj)
    		}
    	}.bind(this))
    }
  }
}
</script>

