<template>
 	<div class="content">
 		<label><input type="file" accept="image/png, image/jpeg" v-on:change="addPic($event)"></label>
 		<div><br><br></div>
 		<div class="pic-box">
 			<img v-if="object_url" v-bind:src="object_url" alt="">
      <textarea class="text-box" v-model="editText" v-if="object_url"></textarea>
 		</div>
 		<div><br></div>
 		<label><input v-if="object_file" type="button" value="click" v-on:click="drawImages()"></label>
    <div><br></div>
    <canvas ref="myCanvas"></canvas>
    <div><br></div>
    <textarea v-if="base64Url" cols="100" rows="20" v-model="base64Url"></textarea>
 	</div>
</template>

<script>

export default {
  name: 'drawImage',
  data:function(){
  	return {
  		object_file:null,
  		object_url:null,
      editText:'',
      base64Url:''
  	}
  },
  watch:{
  	object_file:function(currVal,oldVal){
  		if(currVal != null){
  			this.getObjectURL()
  		}
  	}
  },
  methods:{
  	addPic:function(e){
  		var theEvent = window.event || e
  		if(theEvent.target.files[0].size>100*1024){
  			alert('100k以内')
  			theEvent.target.value = ''
  		}else{
  			this.object_file = theEvent.target.files[0]
  		}
  	},
  	getObjectURL:function(){
        var url=null     
        if(window.createObjectURL!=undefined){
            url=window.createObjectURL(this.object_file)    
        }else if(window.URL!=undefined){
            url=window.URL.createObjectURL(this.object_file)    
        } else if(window.webkitURL!=undefined){
            url=window.webkitURL.createObjectURL(this.object_file)
        }    
        this.object_url = url
    },
    drawImages:function(){
      var img = new Image() 
      img.src = this.object_url
      img.onload = function(){
        var canvas = this.$refs.myCanvas
        canvas.width = img.width
        canvas.height = img.height
        var ctx = canvas.getContext("2d")
        var ratio = window.devicePixelRatio || 1
        console.log(ratio)
        if (1 != ratio) {
          canvas.style.width = canvas.width + 'px';
          canvas.style.height = canvas.height + 'px';
          canvas.width *= ratio;
          canvas.height *= ratio;
          ctx.scale(ratio, ratio);
        }
        ctx.beginPath()
        ctx.drawImage(img,0,0,img.width,img.height)
        if(this.editText != ''){
          ctx.beginPath()
          ctx.font="400 20px MaisonNeueLIG,Lato,sans-serif,Arial,Helvetica"
          ctx.fillStyle = '#fff'
          ctx.textAlign = 'left'
          var colsWidth = 0
          var lastSubStrIndex = 0
          var initHeight = 20
          for(var i = 0;i<this.editText.length;i++){
            colsWidth+=ctx.measureText(this.editText[i]).width
            if(colsWidth > img.width/2){
              colsWidth=0
              // 按空格换行
              var noSpace = true
              for(var j=i;j>=lastSubStrIndex;j--){
                colsWidth+=ctx.measureText(this.editText[j]).width
                if(this.editText.charAt(j)==" "){
                  ctx.fillText(this.editText.substring(lastSubStrIndex,j),5,initHeight)
                  initHeight+=24
                  lastSubStrIndex=j+1
                  noSpace = false
                  break
                }
              }
              if(noSpace){
                ctx.fillText(this.editText.substring(lastSubStrIndex,i),5,initHeight)
                initHeight+=24
                lastSubStrIndex=i
              }
            }
            if(i==this.editText.length-1){
              ctx.fillText(this.editText.substring(lastSubStrIndex,i+1),5,initHeight)
            }
          }
        }
        this.base64Url = canvas.toDataURL()
      }.bind(this)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.content{padding-top: 40px;}
.pic-box{position: relative;margin: 0 auto;display: inline-block;font-size: 0;}
.text-box{position: absolute;left: 0;top: 0;border: solid 1px #dcdcdc;font-size: 20px;text-align: left;display: inline-block;background-color: inherit;color: #fff;width: 50%;height: 60px;resize: none;line-height: 24px;font-family: MaisonNeueLIG,Lato,sans-serif,Arial,Helvetica;padding-left: 5px;}
</style>



