<template>
	<div>
		<div class="stage" v-show="entrance.length>0">
			<div class="stage-item" v-for="(item,index) in entrance" v-bind:style="currEntrance == index ? {'left':'50%','top':'50%','transform':'translate(-50%,-50%)','zIndex':'999'} : {'left':item.x+'%','top':item.y+'%','transform':'rotate('+item.rotate+'deg) scale(0.7,0.7)'}" v-on:click="currEntrance=index" v-bind:class="{'sm':currEntrance != index }">
				<h2><a v-bind:href="'#'+item.path">{{item.title}}</a></h2>
				<p>{{item.desc}}</p>
			</div>
		</div>
		<div class="page-group">
			<pagegroup v-bind:totalPageNum="entrance.length" v-bind:currPage="currEntrance+1" v-on:changePageChild="changePageParent"></pagegroup>
		</div>
	</div>
</template>

<script>
	import router from '../router'
	import Pagegroup from './Pagegroup'

	export default {
		name: 'home',
		data:function () {
			return {
				entrance:[],
				currEntrance:0
			}
		},
		components : {
			Pagegroup
		},
		created:function(){
			router.options.routes.map(function(item){
				this.entrance.push({title:item.title,desc:item.desc,path:item.path,x:0,y:0,rotate:0})
			}.bind(this))
			this.resetPosition()
		},
		watch:{
			currEntrance:function(){
				this.resetPosition()
			}
		},
		methods:{
			resetPosition:function(){
				if(this.entrance.length>0){
					this.entrance.map(function(item,index){
						if(index%4 == 0){
							item.x = Math.random()*50-10
							item.y = Math.random()*50-10
						}
						if(index%4 == 1){
							item.x = Math.random()*50-10+50
							item.y = Math.random()*50-10
						}
						if(index%4 == 2){
							item.x = Math.random()*50-10
							item.y = Math.random()*50-10+50
						}
						if(index%4 == 3){
							item.x = Math.random()*50-10+50
							item.y = Math.random()*50-10+50
						}
						item.rotate = Math.random()*60-30
					}.bind(this))
				}
			},
			changePageParent:function(data){
				this.currEntrance = data-1
			}
		}
	}
</script>

<style scoped>
.stage{height: 700px;height: 70vh;position: relative;overflow:hidden;background-color: #f8f8f8;}
.stage-item{position: absolute;transition: all 0.5s;width: 290px;height: 170px;box-sizing: border-box;padding: 20px;cursor: pointer;background-color: #fff;box-shadow: 0 0 12px 0 rgba(0,0,0,0.25);border-radius: 4px;}
.stage-item.sm{background-color: rgba(255,255,255,0.3);filter: blur(2px);}
.stage-item a{color: #333;}
.stage-item a:hover{color: #0b8dff;}
.page-group{height: 300px;height: 30vh;box-sizing: border-box;padding-top: 40px;}
</style>


