<template>
	<div>
		<div class="marquee" v-bind:style="{'height':height+'px'}">
			<div class="marquee-item" v-bind:style="{'marginTop':marginTop+'px'}">
				<p v-bind:style="{'height':height/itemNum+'px','lineHeight':height/itemNum+'px'}" v-for="item in dataList">{{item}}</p>
			</div>
		</div>
		<label>
			speed:<input type="text" v-model="speed">
		</label>
	</div>
</template>

<script>

export default {
  name: 'marquee',
  data:function(){
  	return {
  		dataList:[],
  		height:300,
  		itemNum:10,
  		marginTop:0,
  		speed:1
  	}
  },
  created:function(){
  	for(var i=0;i<=20;i++){
  		this.dataList.push(i)
  	}
  	this.marquee()
  },
  watch:{
  	speed:function(){
  		this.marginTop = 0
  	}
  },
  methods:{
  	marquee:function(){
  		requestAnimationFrame(function(){
  			this.marginTop -= this.speed
  			if(Math.abs(this.marginTop)/(this.height/this.itemNum) >= 1){
  				var num = this.dataList.shift()
  				this.marginTop += this.height/this.itemNum
  				this.dataList.push(num)
  			}
  			this.marquee()
  		}.bind(this))
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.marquee{width: 300px;margin: 100px auto;overflow: hidden;}
	.marquee .marquee-item p{background-color: #666;color: #fff;text-align: center;font-size: 14px;}
</style>



