import Vue from 'vue'
import Router from 'vue-router'
import Home from 'components/Home'
import Progress from 'components/Progress'
import Marquee from 'components/Marquee'
import DrawImage from 'components/DrawImage'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      title:'xxx',
      desc:'xxxxxxx'
    },
    {
      path: '/progress-by-canvas',
      name: 'progress',
      component: Progress,
      title:'Progress By Canvas',
      desc:'progress by canvas'
    },
    {
      path: '/marquee',
      name: 'marquee',
      component: Marquee,
      title:'Marquee',
      desc:'marquee'
    },
    {
      path: '/drawImage',
      name: 'drawImage',
      component: DrawImage,
      title:'DrawImage',
      desc:'drawImage by canvas'
    }
  ]
})



